
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    background-image: url(/tic-tac-toe/static/media/backgroundImg.b4f64604.jpg);
    background-position: center;
    background-size: cover;
}
#score {
    padding-top: 5%;
    text-align: center;
    display: block;
    font-size: 22px;
}

#board {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    max-width: 21vw;
}

#board div:nth-child(1), #board div:nth-child(2), #board div:nth-child(3){
    border-top: none;
}

#board div:nth-child(1), #board div:nth-child(4), #board div:nth-child(7){
    border-left: none;
}

#board div:nth-child(3), #board div:nth-child(6), #board div:nth-child(9){
    border-right: none;
}

#board div:nth-child(7), #board div:nth-child(8), #board div:nth-child(9){
    border-bottom: none;
}
#board div {
    width: 7vw;
    height: 7vw;
    border: 2px solid black;
    position: relative;
    cursor: pointer;
}

#board div.playerChoice,
#board div.aiChoice {
    cursor: default;
}


#board div.playerChoice::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 4vw;
    height: 4vw;
    border: 4px solid green;
    border-radius: 50%;
}

#board div.aiChoice::before{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: 7%;
    width: 70%;
    background-color: red;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

#board div.aiChoice::after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: 7%;
    width: 70%;
    background-color: red;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}

#board div.playerWinning {
    -webkit-animation: playerWinning 0.6s linear forwards;
            animation: playerWinning 0.6s linear forwards;
}

#board div.aiWinning {
    -webkit-animation: aiWinning 0.6s linear forwards;
            animation: aiWinning 0.6s linear forwards;
}

@-webkit-keyframes playerWinning {
    0%{
        background-color: transparent;
    }
    100%{
        background-color: yellowgreen;
    }
}

@keyframes playerWinning {
    0%{
        background-color: transparent;
    }
    100%{
        background-color: yellowgreen;
    }
}

@-webkit-keyframes aiWinning {
    0%{
        background-color: transparent;
    }
    100%{
        background-color: lightcoral;
    }
}

@keyframes aiWinning {
    0%{
        background-color: transparent;
    }
    100%{
        background-color: lightcoral;
    }
}
#turn {
    font-size: 20px;
    margin-top: 20px;
}
.notification {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    border: 5px solid black;
    min-width: 70vw;
    min-height: 50vh;
    z-index: 45;
    text-align: center;
    width: 70vw;
}

.notification h1{
    margin: 7%; 
}

.notification label {
    display: block;
    font-size: 20px;
    margin-top: 20px;
}

.notification input{
    font-size: 25px;
    border: 2px solid black;
    padding: 5px;
    margin-top: 16px;
}

.notification p {
    margin-top: 10px;
    font-size: 25px;
}

.notification button{
    display: block;
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    font-size: 20px;
    padding: 10px 20px;
    background-color: white;
    border: 2px solid black;
    cursor: pointer;
    text-transform: uppercase;
}

.notification button:hover{
    background-color: black;
    color: white;
    transition: 0.4s;
}
